.todo__items { padding: 15px; }
.todo__item { padding: 0 35px 0 0; position: relative; }
.todo__add { position: absolute; width: 35px; height: 35px; background: url(i/tick.svg) no-repeat 50% / 15px; right: -5px; top: 0; cursor: pointer; }
.todo__remove { position: absolute; right: -5px; top: -5px; opacity: .5; width: 35px; height: 35px; background: url(i/trash.svg) no-repeat 50% / 15px; cursor: pointer; }
.todo__textarea { border: none; padding: 8px 0 5px 0; border-bottom: 2px solid #ccc; resize: none; width: 100%; font: 400 16px/21px helvetica; border-radius: 0; }
.todo__textarea:focus { border-bottom-color: #09f; }